@import ../../base

// TODO: move these styles to the kratos directory (somewhere)
.kratos__tracking-button
  background: var(--trellis-color-fill-brand-default) !important
  border-color: var(--trellis-color-fill-brand-default) !important
  color: var(--trellis-color-fill-surface-default) !important

  &:hover
    background: var(--trellis-color-fill-brand-hover) !important
    border-color: var(--trellis-color-fill-primary-default) !important

  &:active
    background: var(--trellis-color-fill-primary-default) !important
    border-color: var(--trellis-color-fill-primary-default) !important

  &.inboxsdk__button_disabled
    background: var(--trellis-color-fill-brand-hover) !important
