/* @bend-expect trellis-tokenized */

// This project uses node-sass. There are a few changes to consider: https://git.hubteam.com/HubSpot/Frontend/blob/master/docs/sass.md

// Mixins used by Spacesword
@import ./base

#{$namespace}

  // Any spacesword sass that applies to 'body'. Since any body rules will be scoped to
  // '.hubspot body', they need to be applied to '.hubspot div'
  +set-font-family
  +set-font-size($base-font-size)
  color: $trellis-color-text-core-default
  line-height: rem-calc($trellis-typography-body-100-line-height)

  // Includes utility-mixins and utility-classes used by spacesword.
  @import UIComponents/bundles/project

  // Spacesword styles
  @import HubStyle/stylesheets/hubstyle/hubstyle-theme
  @import UIComponents


  @import react-select-plus/react-select-plus

.hubspot-disable-focus-styles
  #{$namespace}
   *:focus:not(input):not(textarea):not(.keep-default-focus-styles)
     outline: $trellis-border-width-0
