// Fight back against streaks aggressive styling (SIG-2007)
.Kj-JD.hubspot-modal-container
  max-height: inherit

.inboxsdk__modal_fullscreen
  z-index: 9999 // Keep above everything

.kratos__modal_fullscreen
  z-index: 9999 // Keep above everything
  position: fixed
  top: 0px
  left: 0px
  bottom: 0px
  right: 0px
  z-index: 503
  display: flex
  justify-content: center
  align-items: center
  padding: 110px 50px 50px 50px

  .hubspot-modal-container, .kratos__modal-container
    margin-top: -60px
    position: relative
    width: auto

  .kratos__modal_toprow
    height: 6px

  .kratos__modal_close
    margin: 16px

.kratos__modal_overlay
  width: 100%
  height: 100%
  //background-color: rgba(45,62,80,.79)

.Kj-JD
  .inboxsdk__modal_content
    margin-top: 0

  .kratos__modal_content
    margin-top: 0
    padding: 0
