.shepherd-step {
  border-radius: var(--trellis-border-radius-100, 3px);
  background-color: #6a78d1;
  color: #ffffff;
  display: none;
  max-width: 350px;
  width: 100%;
}
.shepherd-step.shepherd-open {
  display: block;
}
.shepherd-step .private-button {
  margin: 24px;
  margin-right: 0;
}
.shepherd-step .private-button.disabled {
  display: none;
}

.shepherd-element {
  z-index: 1000;
}
.shepherd-element::before {
  border-color: transparent;
  border-style: solid;
  border-width: 16px;
  content: "";
  display: block;
  height: 0;
  pointer-events: none;
  position: absolute;
  width: 0;
}
.shepherd-element.shepherd-element-attached-left {
  left: 16px !important;
}
.shepherd-element.shepherd-element-attached-left::before {
  border-right-color: #6a78d1;
  left: -32px;
  top: calc(50% - 16px);
}
.shepherd-element.shepherd-element-attached-right {
  left: -16px !important;
}
.shepherd-element.shepherd-element-attached-right::before {
  border-left-color: #6a78d1;
  right: -32px;
  top: calc(50% - 16px);
}
.shepherd-element.shepherd-element-attached-top {
  top: 6px !important;
}
.shepherd-element.shepherd-element-attached-bottom {
  top: -16px !important;
}
.shepherd-element.shepherd-element-attached-bottom::before {
  border-top-color: #6a78d1;
  bottom: -32px;
  right: calc(50% - 16px);
}

.shepherd-target.shepherd-enabled {
  overflow: visible;
}
.shepherd-target.shepherd-enabled.shepherd-element-attached-top {
  position: relative;
}
.shepherd-target.shepherd-enabled.shepherd-element-attached-top::after {
  border-bottom-color: #6a78d1;
  right: calc(50% - 16px);
  top: 13px;
}
.shepherd-target.shepherd-enabled::after {
  border-color: transparent;
  border-style: solid;
  border-width: 16px;
  content: "";
  display: block;
  height: 0;
  pointer-events: none;
  position: absolute;
  width: 0;
}

.shepherd-content {
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.1);
  padding: 24px;
}
.shepherd-content header {
  font-weight: bold;
}
.shepherd-content .shepherd-text {
  margin-bottom: var(--trellis-space-500, 20px);
}
.shepherd-content .shepherd-text p:last-child {
  margin-bottom: var(--trellis-space-0, 0px);
}
.shepherd-content .shepherd-buttons {
  background-color: #ffffff;
  list-style: none;
  margin: 24px -24px -24px;
  padding: 0;
}
.shepherd-content .shepherd-buttons > li {
  display: inline-block;
}

.shepherd-title {
  font-family: var(--trellis-typography-body-100-font-family, Lexend Deca, Helvetica, Arial, sans-serif);
  font-weight: var(--trellis-typography-heading-100-font-weight, 600);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin-bottom: var(--trellis-space-400, 16px);
  margin-top: var(--trellis-space-0, 0px);
  display: block;
  font-size: var(--trellis-typography-heading-100-font-size, 16px);
  line-height: normal;
  text-transform: none;
  margin-bottom: var(--trellis-space-300, 12px);
}

