/* @bend-expect trellis-tokenized */
// Won't tokenize for now. Transitional components should be used instead.

////
/// @group elements
/// @author HubSpot/faast
////

@import HubStyle/stylesheets/hubstyle/base


$shepherd-padding: $distance-measurement-medium

.shepherd-step
  +set-border-radius
  background-color: $color-thunderdome
  color: $color-olaf
  display: none
  max-width: rem-calc(350px)
  width: 100%

  &.shepherd-open
    display: block

  .private-button
    margin: rem-calc($shepherd-padding)
    margin-right: 0

    &.disabled
      display: none

.shepherd-element
  z-index: $earth-layer

  &::before
    border-color: transparent
    border-style: solid
    border-width: rem-calc(16px)
    content: ''
    display: block
    height: 0
    pointer-events: none
    position: absolute
    width: 0

  &.shepherd-element-attached-left
    left: rem-calc(16px) !important

    &::before
      border-right-color: $color-thunderdome
      left: rem-calc(32px * -1)
      top: calc(50% - #{rem-calc(16px)})

  &.shepherd-element-attached-right
    left: rem-calc(16px * -1) !important

    &::before
      border-left-color: $color-thunderdome
      right: rem-calc(32px * -1)
      top: calc(50% - #{rem-calc(16px)})

  &.shepherd-element-attached-top
    top: rem-calc(6px) !important

  &.shepherd-element-attached-bottom
    top: rem-calc(16px * -1) !important

    &::before
      border-top-color: $color-thunderdome
      bottom: rem-calc(32px * -1)
      right: calc(50% - #{rem-calc(16px)})

.shepherd-target.shepherd-enabled
  overflow: visible

  &.shepherd-element-attached-top
    position: relative

    &::after
      border-bottom-color: $color-thunderdome
      right: calc(50% - #{rem-calc(16px)})
      top: rem-calc(13px)

  &::after
    border-color: transparent
    border-style: solid
    border-width: rem-calc(16px)
    content: ''
    display: block
    height: 0
    pointer-events: none
    position: absolute
    width: 0

.shepherd-content
  box-shadow: $tooltip-shadow
  padding: rem-calc($shepherd-padding)

  header
    font-weight: bold

  .shepherd-text
    +m-bottom-5

    p:last-child
      +m-bottom-0

  .shepherd-buttons
    background-color: $color-olaf
    list-style: none
    margin: rem-calc($shepherd-padding) rem-calc($shepherd-padding * -1) rem-calc($shepherd-padding * -1)
    padding: 0

    > li
      display: inline-block

.shepherd-title
  +set-heading(heading-5)
  +m-bottom-3
