/* @bend-expect trellis-tokenized */
/* We don't tokenize this file because it's a reset. */

#{$namespace}

  // RESET styles
  // We don't want to target the children of an `svg` element because it may cause styling issues.
  // See https://git.hubteam.com/HubSpot/NamespacedHubStyle/pull/127
  *:not(svg *)
    margin: 0
    padding: 0
    border-color: currentColor
    border-style: none
    border-width: medium
    background: transparent
    text-decoration: none
    text-shadow: none
    color: inherit
    float: none
    min-width: initial
    max-width: none
    text-transform: inherit
    letter-spacing: normal
    width: auto
    height: auto
    cursor: inherit
    -webkit-tap-highlight-color: transparent
    text-align: inherit
    max-width: none
    -moz-appearance: initial
    -webkit-appearance: initial

  *:not(:focus)
    outline: 0
