/* @bend-expect trellis-tokenized */

@import HubStyleTokens/stylesheets/tokens

.phone-number-input
  width: 100%
  display: inline-flex

  .phone-number-input-inner
    display: inline-flex

  .extension-input
    display: inline-block
    max-width: 100px
    margin-left: $trellis-space-200

  .country-select
    display: inline-block
    border-bottom-right-radius: $trellis-border-radius-0
    border-top-right-radius: $trellis-border-radius-0
    position: relative
    width: auto

  .phone-text-input
    display: inline-block
    width: auto
    margin-left: $trellis-space-50

.country-dropdown
  min-width: 300px

