// These styles look like they're no longer in use, but I'm nervous to remove them as a part of the trellis migration
//   For now, we can just mark this as "won't fix" since it's not strictly related to the migration
.kratos__composeButtonContainer
  margin-bottom: 3px
  white-space: nowrap

.kratos__composeButton
  height: 20px
  width: 20px

.kratos__button_icon
  height: 17px
  width: 17px

.kratos__button_iconImg
  height: 17px
  width: 17px
  object-fit: contain

.kratos__menu
  padding: 0px !important
  border: 1px solid rgba(0, 0, 0, 0.2)
  font-size: 13px

.kratos__menu > .kratos__menuContent
  padding: 0
  transition: opacity 0.218s
  background: #fff
  outline: none
